export const initState = {
  page: "home",
  playerCount: 24,
  players: [],
  cases: {},
  results: {},
  gameState: "notReady",
  legs: [],
};

export const reducer = (state, action) => {
  const bla = () => console.log("from reducer", state);
  switch (action.type) {
    case "INC_PLAYERS":
      return {
        ...state,
        playerCount: state.playerCount + 1,
      };
    case "DEC_PLAYERS":
      return {
        ...state,
        playerCount: state.playerCount - 1,
      };
    case "ENTER_GAME":
      return {
        ...state,
        page: "game",
        players: getRandomPlayers(state.playerCount, data),
        cases: resetCase(state.playerCount),
        legs: getRandomLegs(state.playerCount),
      };
    case "START_GAME":
      return {
        ...state,
        gameState: "playing",
      };
    case "INPUT_CASE":
      return {
        ...state,
        cases: { ...state.cases, [action.idx]: action.value },
      };
    case "CHECK_READY":
      return {
        ...state,
        gameState: action.isReady ? "ready" : "notReady",
      };
    case "GO_HOME":
      return {
        ...state,
        page: "home",
        gameState: "notReady",
      };
    case "GO_RESULT":
      return {
        ...state,
        page: "result",
        gameState: "notReady",
      };
    case "GO_GAME":
      return {
        ...state,
        page: "game",
        gameState: "notReady",
        results: {},
        players: getRandomPlayers(state.playerCount, data),
        cases: resetCase(state.playerCount),
        legs: getRandomLegs(state.playerCount),
      };
    case "UPDATE_RESULT":
      return {
        ...state,
        gameState:
          Object.keys(state.results).length + 1 === state.playerCount
            ? "done"
            : "playing",
        results: { ...state.results, [action.idx]: action.posX },
      };
    default:
      throw new Error("Unhandled action type");
  }
};
